.section-fecha-eventos {
  background-image: url(../images/bg-welcome-eventos.png);
  background-position: top;
  background-repeat: no-repeat;
  background-size: contain;
  padding-top: 100px;
  padding-bottom: 5rem;
  @media (min-width: 992px) {
    padding-top: 150px;
  }
  .content-eventos {
    background-color: $white;
    border-radius: 15px;
    border: 1px solid lightgray;
    padding-top: 1rem;
    padding-bottom: 1rem;
    h2 {
      text-align: center;
      color: $blue;
      font-weight: 600;
    }
    .detalles-eventos {
      @media (max-width: 991.98px) {
        text-align: center;
      }
      .text-blue {
        color: $blue;
        font-weight: 600;
      }
    }
    .col-izq-eventos {
      box-shadow: 29px 1px 10px -28px rgba(142,138,140,1);
    }
    .content-filtro {
      padding: 0 2rem;
    }
    .title-card-evento {
      @media (min-width: 992px) and (max-width: 1399.98px) {
        font-size: .8rem !important;
      }
    }
    .text-card-evento {
      @media (min-width: 992px) and (max-width: 1399.98px) {
        font-size: .6rem !important;
      }
    }
    .bg-footer-card {
      display: flex;
      justify-content: center;
      .btn-green-white {
        margin-right: .2rem;
      }
      .btn-blue-white {
        margin-left: .2rem;
      }
      .btn-green-white,
      .btn-blue-white {
        padding-left: .7rem;
        padding-right: .7rem;
        font-size: .5rem;
        @media (min-width: 768px) {
          font-size: .6rem;
        }
        @media (min-width: 992px) {
          padding-left: .4rem;
          padding-right: .4rem;
        }
      }
    }
  }
  .accordion-item {
    border-radius: 5px;
  }
}