.card-evento {
  border-radius: 15px;
  .btn-green-white,
  .btn-blue-white {
    padding-left: .7rem;
    padding-right: .7rem;
    font-size: .5rem;
    @media (min-width: 768px) {
      font-size: .8rem;
    }
    @media (min-width: 992px) {
      padding-left: 1rem;
      padding-right: 1rem;
    }
  }
  .header-card {
    padding: 1rem;
    display: flex;
    align-items: center;
    .bg-gray {
      background-color: #EAEAEA;
      color: $blue;
      padding: .8rem;
      @media (min-width: 992px) {
        padding: .8rem 1.2rem;
      }
    }
    .title-card-evento {
      font-weight: 600;
      margin-bottom: 0;
      font-size: .8rem;
      @media (min-width: 992px) {
        font-size: 1rem;
      }
    }
    .text-card-evento {
      margin-bottom: 0;
      font-size: .8rem;
      color: $gray-light;
    }
  }
  .content-info {
    position: relative;
    width: 100%;
    padding-top: 80%;
    .img-card-info {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
      position: absolute;
      top: 0;
      border-bottom-left-radius: 15px;
      border-bottom-right-radius: 15px;
    }
    .footer-card {
      position: absolute;
      bottom: 0;
      width: 100%;
      border-bottom-left-radius: 15px;
      border-bottom-right-radius: 15px;
      .icon-card {
        color: $white;
        margin-bottom: 0;
        margin-left: 1rem;
        font-size: .8rem;
        i {
          margin-right: .5rem;
        }
      }
      .bg-footer-card {
        background: #ffffffbb;
        padding: .8rem;
        text-align: center;
      }
    }
  }
}