.filtro-evento {
  .input-group {
    .input-group-text {
      background: $white;
      border-radius: 0;
      border-top-left-radius: 50px;
      border-bottom-left-radius: 50px;
      padding: 0 0.5rem 0 1rem;
      img {
        width: 1rem;
      }
    }
    .form-control {
      background: $white;
      border-left: none;
      border-radius: 0;
      border-top-right-radius: 50px;
      border-bottom-right-radius: 50px;
      padding-left: 0.5rem;
      padding-right: 1rem;
      font-size: 0.8rem;
      &:focus {
        outline: none;
        box-shadow: none;
        border-color: #ced4da;
      }
    }
  }
  .form-select {
    background-image: url(../images/icons/select.png);
    background-size: 44px 40px;
    background-position: right .1rem center;
    color: $gray-light;
    &:focus {
      outline: none;
      box-shadow: none;
      border-color: #EAEAEA;
    }
  }
  .form-select-sm {
    border-radius: 50px;
    margin-top: .8rem;
    padding-left: 1rem;
    background-color: #EAEAEA;
  }
  .text-check {
    font-size: .8rem;
    text-align: start;
    margin-top: 1rem;
  }
  .form-check-input {
    &:focus {
      outline: none;
      box-shadow: none;
    }
    &:checked {
      background-color: $blue;
    }
  }
}